.popup {
    position: fixed;
    top: 20%;
    left: 30%;
    transform: translate(-50%, -50%);
    background-color: white;
    padding: 3rem;
    width: 50rem;
    z-index: 100000;
    box-shadow: 5px 5px 1rem black;
}
.popup h2 {
    margin-bottom: 2rem;
    text-align: center;
}
.popup div {
    display: flex;
}
.popup div h3 {
    margin-right: 1rem;
    margin-bottom: 2rem;
}
.popup .btns {
    transform: translateX(70%);
}
.popup .btns button {
    padding: 1rem 2rem;
    cursor: pointer;
    margin-left: 1rem;
    background-color: transparent;
    transition: all .4s;
}
.popup .btns button:hover {
    background-color: black;
    color: white;
}

@media only screen and (max-width: 955px) {
    .popup .btns {
        transform: translateX(50%);
    }
}
@media only screen and (max-width: 600px) {
    .popup .btns {
        transform: translateX(0%);
    }
    .popup {
        width: 70%;
        top: 20%;
        left: 5%;
    }
}